import { Novedades } from "../../types";

import { Button } from "@material-tailwind/react";

interface props {
    dataObject: Novedades[] | undefined,
    updateDataObject : React.Dispatch<React.SetStateAction<Novedades[] | undefined>>
}

const ItemsNovedades = ({ dataObject, updateDataObject }: props) =>{

  const deleteItem = (index: number )=> { 
    // eslint-disable-next-line no-restricted-globals
    if(confirm('¿Está seguro dar por finalizada la gestión de la novedad?')){
      updateDataObject((prevent: Novedades[]| undefined)=> 
        prevent?.filter((_item, indexF) => indexF !== index )
      ) 
    }
  }

  return (
  <div className="">
    {dataObject?.map((item, index) => (
        <div className="bg-gray-50 m-5 p-5 rounded"  key={index}>
          <p className="font-bold">{item.fecha} {item.hora}</p>
          <p>Unidad - {item.unidad}: Clave - {item.clave}</p>
          <Button 
            key = {index} 
            size="sm" 
            color="green" 
            variant="gradient"
            onClick={()=> deleteItem(index)}
            >
              Ok
            </Button>
        </div>
    ))}
  </div> 
)}          

export default ItemsNovedades;